.clearfix:after {
    content: "";
    display: table;
    clear: both;
}

.order-invoice a {
    color: #5d6975;
    text-decoration: underline;
}

.order-invoice header {
    padding: 10px 0;
    margin-bottom: 30px;
}

#logo {
    text-align: center;
    margin-bottom: 10px;
}

#logo img {
    width: 180px;
}

.order-invoice h1 {
    border-top: 1px solid #5d6975;
    border-bottom: 1px solid #5d6975;
    color: #5d6975;
    font-size: 2.4em;
    line-height: 1.4em;
    font-weight: normal;
    text-align: center;
    margin: 0 0 20px 0;
   
}

#project {
    float: left;
}

#project span {
    color: #5d6975;
    text-align: right;
    width: 52px;
    margin-right: 10px;
    display: inline-block;
    font-size: 0.8em;
}

#company {
    float: right;
    text-align: right;
}

#project div,
#company div {
    white-space: nowrap;
}

.order-invoice table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    margin-bottom: 20px;
}

.order-invoice table tr:nth-child(2n-1) td {
    background: #f5f5f5;
}

.order-invoice table th,
.order-invoice table td {
    text-align: center;
}

.order-invoice table th {
    padding: 5px 20px;
    color: #5d6975;
    border-bottom: 1px solid #c1ced9;
    white-space: nowrap;
    font-weight: normal;
}

.order-invoice table .service,
.order-invoice table .desc {
    text-align: left;
}

.order-invoice table td {
    padding: 20px;
    text-align: right;
}

.order-invoice table td.service,
.order-invoice table td.desc {
    vertical-align: top;
}

.order-invoice table td.unit,
.order-invoice table td.qty,
.order-invoice table td.total {
    font-size: 1.2em;
}

.order-invoice table td.grand {
    border-top: 1px solid #5d6975;
}

#notices .notice {
    color: #5d6975;
    font-size: 1.2em;
}

.order-invoice footer {
    color: #5d6975;
    width: 100%;
    height: 30px;
    border-top: 1px solid #c1ced9;
    padding: 8px 0;
    text-align: center;
}